:root {
    --background: #ffffff; /* Blanc */
    --foreground: #000000; /* Noir */
    --primary: #0070f3;
    --secondary: #1a1a1a;
}

@media (prefers-color-scheme: dark) {
    :root {
        --background: #ffffff;
        --foreground: #0070f3;
        --primary: #0070f3;
        --secondary: #ededed;
    }
}

html, body {
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
    display: flex;
    flex-direction: column;
    color: var(--foreground);
    background: var(--background);
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.header {
    background-color: var(--background);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 1rem 2rem;
}

.header-content {
    display: flex;
    align-items: center;
    justify-content: flex-start; /* Align items to the left */
    width: 100%;
}

.logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: var(--primary);
    margin-right: 2rem; /* Add some space between the logo and the nav */
}

nav {
    display: flex;
}

nav ul {
    list-style: none;
    display: flex;
    gap: 1rem;
    margin: 0;
    padding: 0;
}

nav ul li {
    margin: 0;
}

nav ul li a {
    text-decoration: none;
    color: var(--foreground);
    font-weight: 500;
}

nav ul li a:hover {
    color: var(--primary);
}

.login-button {
    background-color: var(--primary);
    color: white;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    text-decoration: none;
    font-weight: 500;
    transition: background-color 0.3s ease;
    margin-left: auto; /* Push the login button to the right */
}

.login-button:hover {
    background-color: #005bb5;
}

.bottom-bar {
    width: 100%;
    height: 3px;
    background-color: var(--primary);
    margin-top: 0rem;
}

.contact-form,
.inscription-form,
.login-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem;
    background-color: var(--background);
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-group label {
    margin-bottom: 0.5rem;
    font-weight: bold;
}

.form-group input {
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    color: var(--foreground);
    background-color: var(--background);
    transition: border-color 0.3s ease;
    width: 100%; /* Make input fields take full width */
}

.form-group input:focus {
    border-color: var(--primary);
    outline: none;
}

button[type="submit"] {
    padding: 0.75rem;
    border: none;
    border-radius: 4px;
    background-color: var(--primary);
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button[type="submit"]:hover {
    background-color: #005bb5;
}

footer {
    text-align: center;
    padding: 1rem;
    background-color: var(--background);
    box-shadow: 0 -4px 6px rgba(0, 0, 0, 0.1);
}

/* New styles to center the form */
.center-container {
    display: flex;
    justify-content: center;
    align-items: flex-start; /* Align items to the top */
    min-height: 100vh;
    padding-top: 2rem; /* Add some padding to the top */
}

/* Offres.css */
.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
    text-align: center;
    color: #333;
}

ul {
    list-style-type: none;
    padding: 0;
}

/* Style principal du conteneur */
.offres-container {
    min-height: 100vh;
    background-color: #f8fafc;
    padding: 2rem;
}

/* Titre de la page */
.offres-header {
    max-width: 1400px;
    margin: 0 auto 3rem auto;
    text-align: center;
}

.offres-header h1 {
    color: #1a237e;
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
    font-weight: 700;
}

.offres-header p {
    color: #64748b;
    font-size: 1.2rem;
}

/* Filtres des offres */
.offres-filters {
    max-width: 1400px;
    margin: 0 auto 2rem auto;
    background: white;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
}

.filters-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1.5rem;
}

.filter-item {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
}

.filter-item label {
    color: #1a237e;
    font-weight: 600;
    font-size: 0.95rem;
}

.filter-chips {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-top: 0.5rem;
}

.filter-chip {
    background: #e3f2fd;
    color: #1a237e;
    padding: 0.4rem 0.8rem;
    border-radius: 20px;
    font-size: 0.9rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    cursor: pointer;
    transition: all 0.2s ease;
}

.filter-chip:hover {
    background: #bbdefb;
}

.filter-chip button {
    background: none;
    border: none;
    color: #1a237e;
    cursor: pointer;
    padding: 0;
    display: flex;
    align-items: center;
}

.duration-inputs {
    display: flex;
    gap: 1rem;
    align-items: center;
}

.duration-inputs input {
    width: 80px;
}

.month-select {
    padding: 0.8rem;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    font-size: 1rem;
    color: #1a237e;
    background-color: white;
    cursor: pointer;
    transition: all 0.2s ease;
}

.month-select:hover {
    border-color: #1a237e;
}

.filter-input {
    padding: 0.8rem;
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    font-size: 1rem;
    transition: all 0.2s ease;
}

.filter-input:focus {
    outline: none;
    border-color: #1a237e;
    box-shadow: 0 0 0 2px rgba(26, 35, 126, 0.1);
}

/* Modifier la grille des offres */
.offres-grid {
    max-width: 1400px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 2rem;
}

/* Ajuster le style des cartes d'offres */
.offre-card {
    background: white;
    border-radius: 12px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.08);
    transition: all 0.2s ease;
    border: 1px solid #e2e8f0;
    overflow: hidden;
}

.offre-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.12);
    border-color: #1a237e;
}

/* En-tête de l'offre */
.offre-header {
    padding: 1.5rem;
    border-bottom: 1px solid #e2e8f0;
    background: #f8fafc;
}

.offre-title {
    color: #1a237e;
    font-size: 1.3rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.offre-company {
    color: #64748b;
    font-size: 1rem;
}

/* Contenu de l'offre */
.offre-content {
    padding: 1.5rem;
}

.offre-details {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 1.5rem;
}

.detail-item {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #64748b;
}

.detail-item svg {
    color: #1a237e;
}

/* Actions de l'offre */
.offre-actions {
    display: flex;
    gap: 1rem;
    padding: 1.5rem;
    border-top: 1px solid #e2e8f0;
}

.btn {
    padding: 0.6rem 1.2rem;
    border-radius: 8px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    flex: 1;
    text-align: center;
}

.btn-primary {
    background-color: #1a237e;
    color: white;
    border: none;
}

.btn-primary:hover {
    background-color: #283593;
    transform: translateY(-1px);
}

.btn-outline {
    background-color: transparent;
    border: 1px solid #1a237e;
    color: #1a237e;
}

.btn-outline:hover {
    background-color: #f8fafc;
    transform: translateY(-1px);
}

@media (max-width: 768px) {
    .offres-container {
        padding: 1rem;
    }

    .offres-header h1 {
        font-size: 2rem;
    }

    .offres-grid {
        grid-template-columns: 1fr;
    }
}

.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 200px;
    padding: 2rem;
}

.loading-spinner {
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid var(--primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 1rem;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.error-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 200px;
    padding: 2rem;
}

.error-content {
    background-color: #fff1f1;
    border: 1px solid #ff4444;
    border-radius: 8px;
    padding: 2rem;
    text-align: center;
    max-width: 500px;
}

.error-content h2 {
    color: #ff4444;
    margin-bottom: 1rem;
}

.error-content p {
    color: #333;
    margin: 0;
}

.offres-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 1200px;
    margin: 0 auto;
    padding: 2rem;
    background-color: var(--background);
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.offre .form-group {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.submit-button {
    padding: 0.75rem;
    border: none;
    border-radius: 4px;
    background-color: var(--primary);
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%;
    margin-top: 1rem;
}

.submit-button:hover {
    background-color: #005bb5;
}

@media (max-width: 768px) {
    .offres-form {
        padding: 1rem;
    }

    .offres-grid {
        grid-template-columns: 1fr;
    }
}
